html {
  box-sizing: border-box;
  font-size: 62.5%;
  scroll-behavior: smooth;
}

*, *::before, *::after {
  box-sizing: inherit;
}

body {
  font-size: 1.6rem;
  line-height: 1.5;
  font-family: "Montserrat", sans-serif;
}

h1, h2, h3 {
  margin: 0 0 1rem 0;
}

h1 {
  font-size: 5rem;
}

h2 {
  font-size: 4rem;
}

h3 {
  font-size: 3rem;
}

p {
  margin: 0;
  font-family: "Hind Siliguri", sans-serif;
  color: #5a6770;
}

a {
  text-decoration: none;
  color: #5a6770;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

.contenedor {
  max-width: 120rem;
  width: 90%;
  margin: 0 auto;
}

.text-center {
  text-align: center;
}

.seccion {
  padding: 5rem 0;
}

.header {
  background-color: #fff;
  padding: 2rem 0;
  box-shadow: 0px 0px 3px 0px rgba(0, 0, 0, 0.5);
  position: relative;
}
@media (min-width: 992px) {
  .header {
    padding: 0.5rem 0;
  }
}
.header__grid {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 992px) {
  .header__grid {
    flex-direction: row;
    justify-content: space-between;
  }
}
.header__logo {
  max-width: 24rem;
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .header__logo {
    margin-bottom: 0;
  }
}

.navegacion {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
}
@media (min-width: 768px) {
  .navegacion {
    flex-direction: row;
    gap: 2rem;
    width: auto;
  }
}
.navegacion__enlace {
  margin-bottom: 1rem;
  font-size: 1.8rem;
  font-weight: 700;
  padding: 1rem;
  width: 100%;
  text-align: center;
  position: relative;
}
.navegacion__enlace::after {
  content: "";
  position: absolute;
  bottom: 1rem;
  left: 0;
  height: 0.2rem;
  width: 0;
  transition: width 0.4s;
}
.navegacion__enlace:hover::after {
  background-color: #c2d600;
  width: 100%;
}
.navegacion__enlace:last-of-type {
  margin-bottom: 0;
}
@media (min-width: 768px) {
  .navegacion__enlace {
    margin-bottom: 0;
    width: auto;
  }
}
.navegacion__enlace--footer {
  font-size: 1.6rem;
}
@media (min-width: 992px) {
  .navegacion__enlace--footer:first-of-type {
    padding-left: 0;
  }
}

.hero {
  background-image: linear-gradient(to right, rgba(0, 0, 0, 0.5) 0%, rgba(0, 0, 0, 0.5) 100%), url(../img/hero.webp);
  background-repeat: no-repeat;
  background-position: bottom center;
  background-size: cover;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 30rem;
}
@media (min-width: 768px) {
  .hero {
    min-height: 55rem;
  }
}
.hero__contenido {
  color: #fff;
  text-align: center;
  height: 100%;
}
.hero__heading {
  position: relative;
  display: inline-block;
  font-size: 4rem;
  line-height: 1;
  z-index: 2;
}
@media (min-width: 768px) {
  .hero__heading {
    font-size: 9rem;
  }
}
.hero__heading::before {
  content: "#";
  position: absolute;
  z-index: -1;
  top: -3.5rem;
  left: 1.5rem;
  font-size: 8rem;
  color: #00A9C4;
}
@media (min-width: 768px) {
  .hero__heading::before {
    top: -6rem;
    left: -4rem;
    font-size: 15rem;
  }
}
@media (min-width: 992px) {
  .hero__heading::before {
    top: -8rem;
    left: -6rem;
    font-size: 18rem;
  }
}
.hero__heading::after {
  content: "";
  position: absolute;
  bottom: 0;
  right: 0;
  width: 12rem;
  height: 0.5rem;
  background-color: #00A9C4;
}
@media (min-width: 768px) {
  .hero__heading::after {
    width: 30rem;
    height: 0.8rem;
  }
}
.hero__span {
  display: block;
  padding-left: 15rem;
  margin-bottom: 1rem;
  color: #c2d600;
}

.intendencia {
  padding: 5rem 0;
}
.intendencia::after {
  content: "Raúl “Chuli” Jorge";
  text-align: center;
  font-weight: 700;
  font-size: 2.5rem;
  padding-top: 1.8rem;
  display: block;
  position: relative;
  height: 8rem;
  color: #fff;
  background-color: #c2d600;
  background-size: 20%;
  border-radius: 0 5rem 5rem 0;
  margin-top: 2rem;
  width: 35rem;
}
@media (min-width: 768px) {
  .intendencia::after {
    width: 70rem;
    font-size: 4rem;
    padding-top: 1rem;
  }
}
.intendencia__contenido {
  display: grid;
  grid-template-rows: 1fr 40rem;
  position: relative;
}
@media (min-width: 992px) {
  .intendencia__contenido {
    grid-template-rows: unset;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}
.intendencia__header {
  grid-column: 2/2;
}
.intendencia__top {
  margin-bottom: 2rem;
}
.intendencia__heading {
  line-height: 1.2;
  color: #00A9C4;
  font-size: 2rem;
  font-size: 3rem;
}
@media (min-width: 768px) {
  .intendencia__heading {
    font-size: 3rem;
  }
}
@media (min-width: 992px) {
  .intendencia__heading {
    font-size: 4rem;
  }
}
.intendencia__heading::after {
  content: "";
  display: block;
  background-image: url(../img/header-contacto.png);
  background-repeat: no-repeat;
  height: 1rem;
  width: 60%;
  margin-top: 1rem;
}
@media (min-width: 768px) {
  .intendencia__heading {
    font-size: 4rem;
  }
}
.intendencia__heading::after {
  width: 20rem;
}
.intendencia__descripcion {
  font-size: 2rem;
  font-weight: 700;
  color: #c2d600;
}
@media (min-width: 992px) {
  .intendencia__descripcion {
    font-size: 2.3rem;
  }
}
.intendencia__descripcion--body {
  font-weight: 400;
  color: #5a6770;
  border-block: 1px solid #e1e1e1;
  padding-block: 2rem;
}
@media (min-width: 992px) {
  .intendencia__descripcion--body {
    font-size: 2rem;
  }
}
.intendencia__picture {
  position: absolute;
  bottom: -3rem;
  left: -3rem;
}
@media (min-width: 768px) {
  .intendencia__picture {
    left: 15rem;
  }
}
@media (min-width: 992px) {
  .intendencia__picture {
    left: 10rem;
  }
}
.intendencia__imagen {
  max-width: 30rem;
  transform: rotateY(3.142rad);
}

.destacados {
  background-image: url(../img/trofeo_2.jpg);
  background-position: 5rem 25rem;
  background-repeat: no-repeat;
  background-size: 140%;
}
@media (min-width: 768px) {
  .destacados {
    background-position: 25rem 5rem;
    background-size: 108%;
  }
}
@media (min-width: 992px) {
  .destacados {
    background-position: 57rem -10rem;
    background-size: 70%;
  }
}
@media (min-width: 768px) {
  .destacados__grid {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    gap: 3rem;
  }
}
.destacados__contenido {
  padding: 5rem 0;
}
@media (min-width: 768px) {
  .destacados__contenido {
    grid-column: 1/5;
  }
}
@media (min-width: 992px) {
  .destacados__contenido {
    grid-column: 1/4;
  }
}
.destacados__heading {
  line-height: 1.2;
  color: #00A9C4;
  font-size: 2rem;
  font-size: 3rem;
}
@media (min-width: 768px) {
  .destacados__heading {
    font-size: 3rem;
  }
}
@media (min-width: 992px) {
  .destacados__heading {
    font-size: 4rem;
  }
}
.destacados__heading::after {
  content: "";
  display: block;
  background-image: url(../img/header-contacto.png);
  background-repeat: no-repeat;
  height: 1rem;
  width: 60%;
  margin-top: 1rem;
}
@media (min-width: 768px) {
  .destacados__heading {
    font-size: 4rem;
  }
}
@media (min-width: 992px) {
  .destacados__heading {
    font-size: 5rem;
  }
}
.destacados__descripcion {
  font-size: 2rem;
  margin: 3rem 0;
  line-height: 1.8;
  color: #000;
}
@media (min-width: 992px) {
  .destacados__descripcion {
    font-size: 2.2rem;
  }
}
.destacados__enlaces {
  display: flex;
  flex-direction: column;
  gap: 3rem;
}
@media (min-width: 768px) {
  .destacados__enlaces {
    align-items: flex-start;
  }
}
@media (min-width: 992px) {
  .destacados__enlaces {
    flex-direction: row;
  }
}
.destacados__enlace {
  display: block;
  text-align: center;
  background-color: #00A9C4;
  color: #fff;
  font-size: 1.6rem;
  padding: 1rem 5rem;
  text-transform: uppercase;
  font-weight: 700;
}
.destacados__enlace:hover {
  opacity: 0.7;
}
@media (min-width: 768px) {
  .destacados__enlace {
    width: max-content;
    display: inline-block;
  }
}
.destacados__enlace:hover {
  background-color: #0093ab;
  opacity: 1;
}
.destacados__enlace--inscripcion {
  background-color: #c2d600;
}
.destacados__enlace--inscripcion:hover {
  background-color: #abbd00;
}

.menu {
