.seccion {
  text-align: center;
  position: relative;
  padding: 3rem;
  border-radius: 1rem;
  transition: box-shadow 0.3s;
  overflow: hidden;
}
.seccion::before {
  content: "";
  position: absolute;
  background-color: #d3d9dc;
  top: -5rem;
  right: -5rem;
  width: 130%;
  z-index: -1;
  height: 0;
  transition: height 0.5s;
  transform: rotate(15deg);
}
@media (min-width: 992px) {
  .seccion:hover::before {
    height: 160%;
    background-color: #d3d9dc;
  }
}
.seccion__icono {
  font-size: 4rem;
  color: #00A9C4;
  z-index: 2;
}
.seccion:hover .seccion__icono {
  color: #0093ab;
}
.seccion__contenido {
  z-index: 2;
}
.seccion__nombre {
  font-size: 2rem;
  color: #abbd00;
}
.seccion:hover .seccion__nombre {
  color: #c2d600;
}
.seccion__descripcion {
  font-size: 1.8rem;
}

