.noticias__grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(30rem, 1fr));
  gap: 3rem;
}
.noticias__link {
  text-align: center;
}
.noticias__enlace {
  display: block;
  text-align: center;
  background-color: #00A9C4;
  color: #fff;
  font-size: 1.6rem;
  padding: 1rem 5rem;
  text-transform: uppercase;
  font-weight: 700;
  margin-top: 3rem;
}
.noticias__enlace:hover {
  opacity: 0.7;
}
@media (min-width: 768px) {
  .noticias__enlace {
    width: max-content;
    display: inline-block;
  }
}

.noticia__imagen {
  object-fit: cover;
  width: 100%;
  height: 25rem;
}
.noticia__nombre {
  font-size: 2rem;
  padding: 2rem 0;
}
.noticia__fecha {
  font-weight: 700;
  margin-bottom: 1rem;
  color: #c2d600;
}

.violencia {
  background-image: linear-gradient(to right, rgba(0, 0, 0, 0.7) 0%, rgba(0, 0, 0, 0.7) 100%), url(../img/header-genero.jpeg);
  background-repeat: no-repeat;
  background-size: 100%, cover;
  background-position: center, center center;
  padding: 8rem 0;
  color: #fff;
}
@media (min-width: 992px) {
  .violencia__grid {
    grid-template-rows: unset;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 3rem;
  }
}
.violencia__heading {
  font-weight: 400;
  font-size: 3rem;
  margin-bottom: 5rem;
}
@media (min-width: 768px) {
  .violencia__heading {
    font-size: 5rem;
  }
}
@media (min-width: 992px) {
  .violencia__heading {
    margin-bottom: 0;
  }
}
.violencia__negrita {
  text-transform: uppercase;
  font-weight: 700;
}
.violencia__span {
  display: block;
  color: #c2d600;
  font-weight: 700;
}
.violencia__contenido {
  background-color: rgba(225, 225, 225, 0.2);
  backdrop-filter: blur(0.5rem);
  height: max-content;
  padding: 3rem;
  border-radius: 1rem;
}
.violencia__titulo {
  text-transform: uppercase;
  color: #c2d600;
  line-height: 1;
  font-size: 2rem;
}
.violencia__descripcion {
  color: #e1e1e1;
  font-size: 1.8rem;
}
@media (min-width: 768px) {
  .violencia__descripcion {
    font-size: 2.2rem;
  }
}
.violencia__descripcion-salto {
  display: block;
}
.violencia__numero {
  color: #c2d600;
  font-weight: 700;
}
.violencia__imagen {
  filter: brightness(1.3);
  padding-top: 3rem;
  margin: 0 auto;
  max-width: 70%;
}
.violencia__enlace {
  display: block;
  text-align: center;
  background-color: #00A9C4;
  color: #fff;
  font-size: 1.6rem;
  padding: 1rem 5rem;
  text-transform: uppercase;
  font-weight: 700;
  margin-top: 2rem;
  margin-bottom: 3rem;
}
.violencia__enlace:hover {
  opacity: 0.7;
}
@media (min-width: 768px) {
  .violencia__enlace {
    width: max-content;
    display: inline-block;
  }
}
@media (min-width: 992px) {
  .violencia__enlace {
    margin-bottom: 0;
  }
}

.footer {
  background-color: #fff;
  padding: 2rem 0;
}
@media (min-width: 992px) {
  .footer__grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}
.footer__widget--dos {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.footer__imagen-muni {
  max-width: 25rem;
  margin: 0 auto;
}
@media (min-width: 992px) {
  .footer__imagen-muni {
    margin: 0;
  }
}
.footer__copy {
  text-align: center;
  font-size: 1.8rem;
  padding-top: 2rem;
}
.footer__copy-link {
  color: #c2d600;
}
.footer__span {
  font-weight: 700;
  color: #00A9C4;
}
.footer__imagen {
  margin: 3rem auto;
}
@media (min-width: 992px) {
  .footer__imagen {
    padding-top: 2rem;
    margin: 0;
  }
}
.footer__imagen-jujuy {
  max-width: 25rem;
  filter: brightness(0.5);
}
.footer__social {
  display: flex;
  gap: 2rem;
  justify-content: center;
  padding: 2rem 0;
}
@media (min-width: 992px) {
  .footer__social {
    justify-content: flex-start;
  }
}
.footer__enlace {
  font-size: 2.2rem;
}
.footer__contacto {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  gap: 1rem;
}
@media (min-width: 992px) {
  .footer__contacto {
    align-items: flex-start;
  }
}
.footer__flex-contacto {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1rem;
}
@media (min-width: 992px) {
  .footer__flex-contacto {
    flex-direction: row;
  }
}
.footer__icono {
  font-size: 2.2rem;
}
.footer__descripcion {
  font-size: 1.6rem;
}
.footer__mail:hover {
  color: #00A9C4;
}
.footer__face:hover {
  color: #3548f0;
}
.footer__insta:hover {
  color: #c53838;
}
.footer__twitter:hover {
  color: #00A9C4;
}
.footer__youtube:hover {
  color: #e70c0c;
}

.footer-nav {
  flex-direction: column;
}
@media (min-width: 992px) {
  .footer-nav {
    flex-direction: row;
  }
}

.contacto {
  background-color: rgba(225, 225, 225, 0.5);
}
.contacto__hero {
  background-image: linear-gradient(to right, rgba(0, 0, 0, 0.7) 0%, rgba(0, 0, 0, 0.3) 100%), url(../img/header-contacto.png);
  background-repeat: no-repeat;
  background-size: cover;
  height: 40rem;
  display: grid;
  place-items: center;
}
.contacto__heading {
  color: #fff;
  text-transform: uppercase;
}
.contacto__grid {
  padding: 10rem 0;
}

.formulario {
  background-color: #fff;
  border-radius: 1rem;
  max-width: 60rem;
  margin: 0 auto;
  padding: 2rem;
  margin-top: -15rem;
}
.formulario__fieldset {
  border: 1px solid #e1e1e1;
}
.formulario__legend {
  text-transform: uppercase;
  font-weight: 700;
  margin: 0 auto 2rem auto;
  color: #5a6770;
}
.formulario__campo {
  margin-bottom: 2rem;
}
.formulario__label {
  display: block;
  margin-bottom: 0.5rem;
  text-transform: uppercase;
  color: #5a6770;
  font-weight: 700;
}
.formulario__input {
  width: 100%;
  padding: 1rem;
  background-color: #fff;
  border: 1px solid #e1e1e1;
}
.formulario__input--submit {
  display: block;
  text-align: center;
  background-color: #00A9C4;
  color: #fff;
  font-size: 1.6rem;
  padding: 1rem 5rem;
  text-transform: uppercase;
  font-weight: 700;
  border: none;
  margin-top: 2rem;
  cursor: pointer;
}
.formulario__input--submit:hover {
  opacity: 0.7;
}
@media (min-width: 768px) {
  .formulario__input--submit {
    width: max-content;
    display: inline-block;
  }
}
.formulario__area {
  width: 100%;
  height: 15rem;
}